.search{
    display: flex;
    justify-content: center;
    &__form{
        padding: 50px 200px;
        background: linear-gradient(235deg, rgba(144, 187, 187, 0.2), rgba(25,60,190, 0.2));
        border-radius: 20px;
    }
    &__input{
        padding: 10px 15px 10px 15px;
        border: none;
        font-size: 26px;
        line-height: 42px;
        border-radius: 4px;
    }
}